import styled from "styled-components";

import {
	Content, Portal, Root,
} from "@radix-ui/react-popover";

import {
	colors, displayFontSizes, spacingMap, widthSizes,
} from "../styleguide";
import { Typography } from "../Typography";
import { pxToRem } from "../utils/pxToRem";

import { FilterMenuStyleProps } from "./FilterMenu.types";

export const StyledPopoverRoot = styled(Root)<FilterMenuStyleProps>`
	position: relative;
`;

export const StyledPopoverPortal = styled(Portal)<FilterMenuStyleProps>`
	position: absolute;
	background:  ${colors.white};
	width: ${({ $menuWidth }) => $menuWidth || pxToRem(300)};
	transition: opacity .1s cubic-bezier(.215, .61, .355, 1);
`;

export const StyledPopoverContent = styled(Content)<FilterMenuStyleProps>`
	overflow-x: hidden;
	display: flex;
	margin: initial;
	flex-direction: column;
	justify-content: center;
	background: ${colors.white};

	align-items: flex-start;
	position: relative;
	min-width: fit-content;
	height:  ${({ $menuHeight }) => $menuHeight || widthSizes.sr};

	overflow-y: scroll;
	::-webkit-scrollbar{
		display: none;
	}
	width: ${({ $menuWidth }) => $menuWidth || pxToRem(300)};

	box-shadow: 0 ${spacingMap.lg} ${spacingMap.lg} -8px ${colors.black}24,
	0 ${spacingMap.sm} ${spacingMap.sm} ${colors.black}24, 0 ${spacingMap.xs} ${spacingMap.xs} ${colors.black}24,
	0 ${spacingMap.xxs} ${spacingMap.xxs} ${colors.black}24, 0 2px 2px ${colors.black}24;
	border-radius: 8px;
	z-index: ${({ $zIndex }) => $zIndex || 1};
`;


export const InputWrapper = styled.div`
	width: inherit;
	padding: ${spacingMap.sm} ${spacingMap.sm} 0 ${spacingMap.sm};
	box-sizing: border-box;
	position: sticky;
	top: 3px;
	background-color: ${colors.white};
	min-height: max-content;
	&:hover, &:focus-within, &:active, &:focus-visible {
		color:  ${colors.black};
		background: ${colors.white};
		cursor: pointer;
	};
	margin-bottom: ${spacingMap.xs};
`;

export const TopActionsWrapper = styled.div`
	padding: ${spacingMap.sm} 0 ${spacingMap.xs} 0;
`;

export const StyledFilterListItem = styled.li<FilterMenuStyleProps>`
	align-items: center;
	box-sizing: border-box;
	border-radius: 8px;
	width: ${({ $menuWidth }) => $menuWidth || pxToRem(300)};
	height: ${pxToRem(40)};
	flex: none;
	order: 2;
	align-self: stretch;
	display: flex;
	flex-direction: row;
	width: 100%;
	font-family: ${displayFontSizes.bodyCopy.$fontFamily};
	font-size:  ${displayFontSizes.bodyCopy.$fontSize};
	font-weight: ${displayFontSizes.bodyCopy.$fontWeight};
	color: ${colors.black};
	background: ${colors.white};

	&:hover {
		color:  ${colors.midBlue};
		background: ${colors.midBlue}1A;
		cursor: pointer;
		text-decoration: none;
		box-shadow: none;
		outline: none;
	}

	${({ $isSelected }) => $isSelected && `
		color:  ${colors.midBlue};
		background: ${colors.midBlue}1A;
		cursor: pointer;
		text-decoration: none;
		box-shadow: none;
		outline: none;
	`};

	list-style: none;
	padding: ${spacingMap.xs};
	gap: ${pxToRem(10)};
	line-height: ${displayFontSizes.bodyCopy.lineHeight};
	display: flex;
	justify-content: start;

	&:not(:first-child) {
		margin-top: ${spacingMap.xxs};
	}
`;


export const FilterList = styled.ul<FilterMenuStyleProps>`
	box-sizing: border-box;
	list-style: none;
	height:  ${({ $menuHeight }) => $menuHeight || widthSizes.sr};
	width: 100%;
	margin: 0;
	padding: ${spacingMap.sm};
	overflow-y: scroll;
	::-webkit-scrollbar{
		display: none;
	}
`;

export const FilterIconWrapper = styled.div`
	margin-left: auto;
`;

export const NoResultMessage = styled(Typography)`
	color: ${colors.black};
	opacity: 0.66;
	text-align: center;
`;
